import React, {useEffect, useState} from 'react'
import Layout from '../components/layout/Layout.js'
import { Modal, Form, Input, InputNumber, DatePicker, Select, message, } from 'antd';
import TransactionHeading from '../components/TransactionHeading.js';
import axios from 'axios'
import Spinner from '../components/Spinner.js'
import trash from '../images/trash.png'
//arrow function rafce:
const HomePage = () => {
  //form
  const [form] = Form.useForm();
  //add transaction modal menu state
  const [showModal, setShowModal] = useState(false);
  //loading state for uploading transaction
  const [loading, setLoading] = useState(false);
  //state for holding transactions 
  const [allTransactions, setAllTransactions] = useState([]);
  //state for showing filter
  const [showFilter, setShowFilter] = useState(false);
  //state for holding filter values
  const [filterValues, setFilterValues] = useState('clear')
  //amount text states
  const [amountText, setAmountText] = useState('')
  //hide model if transaction is cancelled
  const cancelModel = () =>{
    setShowModal(false);
  }
  //handle add-transaction submit
  const handleSubmit = async (values) => {
    try{
      const user = JSON.parse(localStorage.getItem('user'));
      setLoading(true);
      await axios.post('/api/v1/transactions/add-transaction', {...values, userid:user.user._id});
      setLoading(false);
      message.success('Transaction Added!')
      getAllTransactions();
      cancelModel();
    }catch(error){
      setLoading(false);
      cancelModel();
      message.error("Something went wrong!")
    }
  }
//get all user transactions
const getAllTransactions = async () => {
  try{
    const user = JSON.parse(localStorage.getItem('user'))
    setLoading(true);
    const res = await axios.post('/api/v1/transactions/get-transactions', {userid: user.user._id, filterValues})
    setLoading(false);
    setAllTransactions(res.data);
  }catch(error){
      console.log(error);
      setLoading(false);
      message.error('something went wrong')
  }
} 
//use effect hook for passing transactions to the table
useEffect( () => {
  const getAllTransactions = async () => {
    try{
      const user = JSON.parse(localStorage.getItem('user'))
      setLoading(true);
      const res = await axios.post('/api/v1/transactions/get-transactions', {userid: user.user._id, filterValues})
      setLoading(false);
      setAllTransactions(res.data);
    }catch(error){
        console.log(error);
        setLoading(false);
        message.error('something went wrong')
    }
  }
  getAllTransactions();
},[filterValues]);
//Filter Submit Logic: 
const handleFilterSubmit = (values) => {
setFilterValues(values);
setShowFilter(false);
}
//Cancel Filter Logic
const cancelFilter = () =>{
  setShowFilter(false);
}
//clear filter logic
const handleClearFilter = () =>{
  setFilterValues('clear');
  getAllTransactions();
  cancelFilter();

}
  //delete transactions
const handleTransactionDlt = async (value) => {
  try {
   await axios.post(
     "/api/v1/transactions/delete-transaction",
     { transactionID: value }
   );
    message.success("Transaction Deleted");
    getAllTransactions();
  } catch (error) {
    console.log(error);
    message.error("Something Went Wrong");
  }
}
 //setting amount text according to the type value
const handleTypeChange =  (value) => {
 if(value==='Income'){
  setAmountText('text-green-500')
 }else if(value==='Expense'){
  setAmountText('text-red-500')
 }else{
  console.log('invalid input');
 }

}

const getFormFeild = () =>{
  form.setFieldsValue({
    textCol: `${amountText}`
  });
}
  return (
    <Layout>
      {
        //Refresh And Filter Buttons:
      }
      <div className="flex flex-row items-center justify-center">
        <div className="flex flex-row gap-1 items-center justify-between w-3/4">
          <div>
            <button
              className="hover:bg-gray-800 hover:text-white bg-slate-200 rounded-md px-1"
              onClick={getAllTransactions}
            >
              Refresh
            </button>
          </div>
          <div>
            <button className="mx-5 my-1 hover:bg-gray-800 hover:text-white rounded-md bg-slate-200" onClick={() => {setShowFilter(true)}}>
              <span className="mx-1">Filter</span>
            </button>
          </div>
        </div>
      </div>
      {
        //Main Transaction Table Here:
      }
      <div>
        <TransactionHeading />
      </div>
      {
      //Transaction Boxes Added With .map ES6 Feature
      }
      <div>
      {
          allTransactions.map((itm, index)=>(
          
            <div key={`transactionDiv1${itm._id}`} className="flex justify-center items-center">
            <div key={`transactionDiv2${itm._id}`} className="grid grid-cols-4 gap-1 bg-slate-200 rounded-md my-1 mx-1 h-auto w-3/4 shadow-md">
              <div key={`transactionDiv3${itm._id}`} className="mx-1">
                <p key={`transactionPara1${itm._id}`} className="responsiveText">{itm.type}</p>
              </div>
              <div key={`transactionDiv4${itm._id}`}>
                <p key={`transactionPara2${itm._id}`} className="responsiveText ">{itm.discription}</p>
              </div>
              <div key={`transactionDiv5${itm._id}`}>
                <p key={`transactionPara3${itm._id}`} className="responsiveText">{itm.date.slice(0,10)}</p>
              </div>
              <div key={`transactionDiv6${itm._id}`} className="flex flex-nowrap justify-end">
                <p key={`transactionPara4${itm._id}`} className={`mx-2 responsiveText ${itm.textCol} `}>₹{itm.amount}</p>
                {
                  //Delete Button Here:
                }
                <button key={`transactionButton1${itm._id}`} type='button' className="rounded-md px-1 hover:bg-red-300" onClick={()=>handleTransactionDlt(itm._id)}>
                  <img key={`transactionImg1${itm._id}`} className="w-4 h-4 responsiveIcon" src={trash} alt="delete icon" />
                </button>
              </div>
            </div>
          </div>
          ))
        }
      </div>
      {
        //Add Button And Loading Icon Here:
      }
      <div className="flex justify-center items-center my-1">
        <button
          className="hover:bg-gray-300 rounded-md py-1"
          onClick={() => {
            setShowModal(true);
          }}
        >
          <span className="bg-gray-800 text-white px-1 mx-1 rounded-full">
            +
          </span>
          <span className="mr-1">Add</span>
        </button>
      </div>
      <div className="flex justify-center items-center my-1">
        {loading && <Spinner />}
      </div>
      {
        //Modal Form For New Transactions Starts Here
      }
      <Modal
        open={showModal}
        onCancel={cancelModel}
        footer={false}
        title="Add New Transaction"
        >
        <Form layout="vertical" onFinish={handleSubmit} form={form}>
          <Form.Item label="Type" name="type"  >
            <Select onChange={handleTypeChange} options={[
      {
        value: 'Income',
        label: 'Income',
      },
      {
        value: 'Expense',
        label: 'Expense',
      },
    ]}/>
             
          </Form.Item>
          <Form.Item onClick={getFormFeild} label="Discription" name="discription" >
            <Input type="text" />
          </Form.Item>
          <Form.Item onClick={getFormFeild} label="Date" name="date">
            <DatePicker />
          </Form.Item>
          <Form.Item onClick={getFormFeild} label="₹ Amount" name="amount">
            <InputNumber />
          </Form.Item>
          <Form.Item className='hidden' name='textCol'>
            <Input />
          </Form.Item>
          <div className="flex justify-end">
            <button
              type="submit"
              className="hover:bg-gray-800 hover:text-white border-2 border-gray-800 rounded-md"
            >
              <span className="mx-1">Save</span>
            </button>
          </div>
        </Form>
        {loading && <Spinner />}
      </Modal>
      {
        //Modal Form For Filter Starts Here
      } 
      <Modal open={showFilter} footer={false} title='Filter' onCancel={cancelFilter}>
        <Form onFinish={handleFilterSubmit}>
      <Form.Item label="type" name="type">
            <Select>
              <Select.Option value="Income">Income</Select.Option>
              <Select.Option value="Expense">Expense</Select.Option>
            </Select>
          </Form.Item>
          <Form.Item label="date" name="date">
            <Select>
              <Select.Option value="7">Last Week</Select.Option>
              <Select.Option value="30">Last Month</Select.Option>
              <Select.Option value="365">Last Year</Select.Option>
            </Select>
          </Form.Item>
          <div className="flex justify-between">
          <button type='button'
              className="rounded-md hover:bg-gray-800 hover:text-white border-2 border-gray-800"
            >
              <span className="mx-1" onClick={handleClearFilter}>Clear Filters</span>
            </button>
            <button
              type="submit"
              className="hover:bg-gray-800 hover:text-white border-2 border-gray-800 rounded-md"
            >
              <span className="mx-1">Done</span>
            </button>
          </div>
          </Form>
      </Modal>
    </Layout>
  );
}
export default HomePage;
