import React from 'react'
import {useState, useEffect} from 'react'
import Layout from '../components/layout/Layout.js'
import { Form, Input , message } from 'antd';
import { Link, useNavigate } from 'react-router-dom';
import Spinner from '../components/Spinner.js';
import axios from 'axios';


const Login = () => {
    const [loading, setLoading]=useState(false);
    const navigate = useNavigate(); 
//functions on form submission, values going to backend with axios api
    const submitHandler = async (values) => {
        try{
            setLoading(true);
            const {data} = await axios.post('/api/v1/users/login', values);
            message.success('Login Success');
            localStorage.setItem('user', JSON.stringify({...data}))
            setLoading(false);
            navigate('/')
        }catch(error) {
            setLoading(false);
            message.error('Invalid Username or Password ');

        }
//if user already exists in local storage, useEffect will navigate them to the home page. 
    };
    useEffect(()=>{
        if(localStorage.getItem('user')){
            navigate('/')
        }
    },[navigate]);


  return (
    <>
   
    <Layout>
    
    <div className='flex justify-center items-center h-full'>
    {loading && <Spinner/>}
    <Form layout='vertical' onFinish={submitHandler}>
    <h1 className='text-center font-bold'>Login</h1>
        <Form.Item label='email' name='email'>
            <Input/>
        </Form.Item>
        <Form.Item label='password' name='password'>
            <Input/>
        </Form.Item>
        <div className = 'flex justify-center items-center'>
        <button className="text-gray-900 bg-white border border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-4 focus:ring-gray-200 font-medium rounded-full text-sm px-5 py-2.5 me-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 dark:focus:ring-gray-700">Login</button>
        </div>
        <div className = 'flex justify-center items-center'>
        <Link to='/register'>Not a member yet? Click Here</Link>
        </div>
    </Form>
    
      
       
        
    </div>
    </Layout>
    </>
  )
}

export default Login;
