import React,{useEffect, useState} from 'react'
import Layout from '../components/layout/Layout'
import {Modal , Button, message} from 'antd';
import axios from 'axios';
const Profile = () => {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const[deleteConfirm, setDeleteConfirm] = useState(false);
    useEffect(()=>{
        const profileInfo = () =>{
          if(localStorage.getItem('user')){
          const userProfile = JSON.parse(localStorage.getItem('user'));
          const userName = userProfile.user.name;
          const userEmail = userProfile.user.email;
          setName(userName);
          setEmail(userEmail);
        }else{
          message.error('please sign in')
        }
        }
        profileInfo();
    },[])
  const handleUserDelete = () =>{
        setDeleteConfirm(true);
  }
  const handleDeleteCancel = () =>{
    setDeleteConfirm(false);
  }
  const handelDeleteFinal = async () =>{
        try{
          const userProfile = JSON.parse(localStorage.getItem('user'));
          const id= userProfile.user._id;
          console.log(id);
          await axios.post('/api/v1/delete/delete-user',{_id: id, userid: id});
          localStorage.removeItem('user');
          message.success('User Deleted');
          handleDeleteCancel();
          
        }catch(error){
          console.log(error)
          message.error('Delete-failed')
        }
  }
  return (
    <>
    <Layout>
        <div className='flex justify-center items-center mt-5 '>
            <div className='rounded-md bg-slate-200 p-3 w-3/4'>
            <p>Name: {name}</p>
            <p>Email: {email}</p>
           <Button danger className='my-1' onClick={handleUserDelete}>Delete Profile</Button>
            </div>
        </div>
        <Modal open={deleteConfirm} onCancel={handleDeleteCancel} title='Are you sure you want to delete your SpentIt Profile?' footer={false}>
        <div className='flex justify-between items-center'>
        <Button onClick={handleDeleteCancel}>Cancel</Button>
        <Button danger onClick={handelDeleteFinal}>Delete</Button>
        </div>
        </Modal>
    </Layout>
    </>
  )
}

export default Profile
