import React from 'react'
import Layout from '../components/layout/Layout'

const About = () => {
  return (
<>
<Layout>
<div className='flex flex-nowrap justify-center items-center mt-5 h-full'>
        <div className='flex flex-wrap flex-col p-5 w-3/4 rounded-md bg-slate-200 h-full mb-5 responsiveText'>
            <div>
        <h1 className='text-center font-bold'>About SpentIt</h1>
        <p className='text-center'>SpentIt App helps you to keep a record of your daily transactions and manage your expenses better. </p>
        <p className='text-center'>It also provides you with personalized analytics of your expenditure and income for helping you save. </p>
        </div>
        <div>
        <h2 className='font-semibold'>How to use:</h2>
        <ul>
            <li>
                1. Click the + Add button
            </li>
            <li>
                2. Fill the form with type, discription, date and amount
            </li>
            <li>
                3. Your transactions will appear whenever you log in. 
            </li>
            <li>
                4. You can go to the analytics page to view your personalized analytics. 
            </li>
        </ul>
        </div>
        </div>
      </div>

</Layout>
</>
  )
}

export default About
