import React from "react";
import trash from '../images/trash.png'
export default function TransactionHeading() {
  return (
    <>
      <div className="flex justify-center items-center">
        <div className="grid grid-cols-4 gap-1 bg-slate-200 rounded-md my-1 mx-1 h-auto w-3/4 shadow-md flex-wrap">
          <div className="mx-1">
            <p className="responsiveText font-semibold">Type</p>
          </div>
          <div>
            <p className="responsiveText font-semibold">Discription</p>
          </div>
          <div>
            <p className="responsiveText font-semibold">Date</p>
          </div>
          <div className="flex flex-wrap items-center flex-row justify-end">
            <p className="mx-2 responsiveText font-semibold">₹ Amount</p>
            <span className=" rounded-full px-1"><img className='w-4 h-4 mt-1 responsiveIcon' src={trash} alt='delete icon'/></span>
          </div>
        </div>
      </div>
    </>
  );
}
