import React, {useEffect, useState} from 'react'
import Layout from '../components/layout/Layout.js'
import { LineOutlined, PieChartOutlined } from '@ant-design/icons'
import axios from 'axios'
import { Progress } from 'antd'
const Analytics = () => {
  //states
  const [dataForm, setDataForm] = useState('pie');
  const [allTransactions, setAllTransactions] = useState([])
  //useeffect hook to only get the transactions once. 
useEffect(()=>{
  const getData = async () =>{
    try{
    const user = JSON.parse(localStorage.getItem('user'))
    const res = await axios.post('/api/v1/transactions/get-transactions', {userid: user.user._id, filterValues: 'clear'})
    setAllTransactions(res.data);
  }catch(error){
    console.log(error);
  }
  }
  getData();
},[])
//variables to evaluate
const totalTransactions = allTransactions.length;
//income calculations
const totalIncome = allTransactions.filter(transaction => transaction.type === 'Income')
const totalIncomePercent = (totalIncome.length/totalTransactions) * 100;
const incomeArray = [0]
totalIncome.map((item)=>{incomeArray.push(item.amount)})
const sumOfIncome = incomeArray.reduce((sum, value)=>{
  return sum+value;
})
//expense calculations
const totalExpense = allTransactions.filter(transaction => transaction.type === 'Expense')
const totalExpensePercent = (totalExpense.length/totalTransactions) * 100;
const expenseArray = [0];
totalExpense.map((item)=>{expenseArray.push(item.amount)});
const sumOfExpense = expenseArray.reduce((sum, value)=>{
  return sum+value;
})
//total calculations: 
const totalAmount = parseInt(sumOfIncome) + parseInt(sumOfExpense);

const persentIncome = Math.floor((sumOfIncome/totalAmount) *100);
const persentExpense = Math.floor((sumOfExpense/totalAmount) *100);
//showing bar or pie chart according to the user selection
const handleBarChart = () =>{
      setDataForm('bar');
    }
const handlePieChart = () =>{
      setDataForm('pie')
    }
  return (
      <>
      <Layout>
      <div className='flex flex-nowrap justify-center items-center mt-5 h-full'>
        <div className='flex flex-wrap  flex-col p-5 w-3/4 rounded-md bg-slate-200 h-full mb-5 responsiveText'>
        <div className='flex justify-start my-2'>
        <h2 >Chart Type:</h2>
        <LineOutlined  className='hover:cursor-pointer hover:bg-gray-400 rounded-md mx-2'onClick={handleBarChart}/>
        <PieChartOutlined className='hover:cursor-pointer hover:bg-gray-400 rounded-md  mx-2' onClick={handlePieChart}/>
        </div>
        <h2>No. Of Income and Expense Entries: </h2>
        <div className='flex justify-center'>
       
          {dataForm === 'pie' && <Progress type='circle' strokeColor={'green'} percent={totalIncomePercent} className='my-2 mx-1'/>}
          {dataForm === 'pie' && <Progress type='circle' strokeColor={'red'} percent={totalExpensePercent} className='mx-1 my-2'/>}
          {dataForm === 'bar' && <Progress strokeColor={'green'} percent={totalIncomePercent} className='my-2 mx-1'/>}
          {dataForm === 'bar' && <Progress strokeColor={'red'} percent={totalExpensePercent} className='mx-1 my-2'/>}
        </div>
        <h2>Total Income % vs Total Expense %: </h2>
        <div className='flex justify-center'>
        {dataForm === 'pie' && <Progress type='circle' strokeColor={'green'} percent={persentIncome} className='my-2 mx-1'/>}
        {dataForm === 'pie' && <Progress type='circle' strokeColor={'red'} percent={persentExpense} className='mx-1 my-2'/>}
        {dataForm === 'bar' && <Progress strokeColor={'green'} percent={persentIncome} className='my-2 mx-1'/>}
          {dataForm === 'bar' && <Progress strokeColor={'red'} percent={persentExpense} className='mx-1 my-2'/>}
        </div>
        <div>
        <h2>Income = <span className='text-green-800 text-lg mx-2'>•</span> </h2>
        <h2>Expense = <span className='text-red-500 text-lg mx-2'>•</span> </h2>
        <h2 >Total Income: <span className='text-green-500'>{sumOfIncome}</span></h2>
        <h2 >Total expense: <span className='text-red-500'>{sumOfExpense}</span> </h2>
        </div>
        </div>
    
      </div>
      </Layout>
      </>
    
  )
}

export default Analytics
