import React from 'react'
import { useState, useEffect } from 'react'
import Layout from '../components/layout/Layout'
import { Form, Input, message } from 'antd';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import Spinner from '../components/Spinner.js';
const Register = () => {

    const navigate = useNavigate(); 
    const [spinner, setSpinner] = useState(false)
    //functions on form submission, values going to backend with axios api
    const submitHandler = async (values) => {

        try{
                setSpinner(true);
                await axios.post('/api/v1/users/register', values);
                message.success('Registration Successful');
                setSpinner(false);
                navigate('/login')
        }catch(error){
            setSpinner(false);
            message.error('Invalid Username or password' + error);
        }

    }
//if user already exists in application, useEffect to auto redirect them to the home page
    useEffect(()=>{
        if(localStorage.getItem('user')){
            navigate('/')
        }
    },[navigate])

  return (
    <>
    
    <Layout>
    {spinner && <Spinner/>}
    <div className='flex justify-center items-center h-full'>
   
    <Form layout='vertical' onFinish={submitHandler}>
    <h1 className='text-center font-bold'>Registration Form</h1>
    <Form.Item label='name' name='name'>
            <Input/>
        </Form.Item>
        <Form.Item label='email' name='email'>
            <Input/>
        </Form.Item>
        <Form.Item label='password' name='password'>
            <Input/>
        </Form.Item>
        <div className = 'flex justify-center items-center'>
        <button className="text-gray-900 bg-white border border-gray-300 focus:outline-none hover:bg-gray-100 focus:ring-4 focus:ring-gray-200 font-medium rounded-full text-sm px-5 py-2.5 me-2 mb-2 dark:bg-gray-800 dark:text-white dark:border-gray-600 dark:hover:bg-gray-700 dark:hover:border-gray-600 dark:focus:ring-gray-700">Register</button>
        </div>
        <div className = 'flex justify-center items-center'>
        <Link to='/login'>Already a member? Click Here</Link>
        </div>
    </Form>
    
      
       
        
    </div>
    </Layout>
    </>
  )
}

export default Register;
