import React from 'react'
import Layout from '../components/layout/Layout.js'
import SpotifyDesignClone from '../images/SpotifyDesignClone.png'
import SimonGame from '../images/SimonGame.png'
import DrumSet from '../images/DrumSet.png'
import MoveIt from '../images/MoveIt.png'
const Projects = () => {
  return (
    <>
    <Layout>
      <div className='flex flex-nowrap justify-center items-center mt-5 h-full'>
        <div className='flex flex-wrap  justify-between p-5 w-3/4 rounded-md bg-slate-200 h-full mb-5 responsiveText'>
            {
              //card 1: spotify design clone
            }
            <div className='w-40 h-40 '>
              <img className='lg:w-48 lg:h-28 md:w-48 md:h-28 w-16 h-9'src={SpotifyDesignClone} alt='spotify design clone' />
                <a className='font-semibold hover:underline' href='https://anku5x.github.io/spotifydesignclone/'>Spotify Design Clone</a>
                <p>A desktop-only clone of award winning website Spotify Design to learn React concepts like Hooks, State, and Props, CSS concepts like positioning, transitions and color schemes.</p>
            </div>
            {
              //card 2: Simon Game
            }
             <div className='lg:w-40 lg:h-40 w-28 h-28'>
              <img className='lg:w-48 lg:h-28 md:w-48 md:h-28 w-16 h-9' src={SimonGame} alt='simon game'/>
                <a className='font-semibold hover:underline' href='https://anku5x.github.io/simongame/'>Simon Game</a>
                <p>A simple game designed with JavaScript DOM and CSS</p>
            </div>
            {
              //card 3: Drum Set
            }
            <div className='lg:w-40 lg:h-40 w-28 h-28'>
              <img className='lg:w-48 lg:h-28 md:w-48 md:h-28 w-16 h-9' src={DrumSet} alt='drum set'/>
                <a className='font-semibold hover:underline' href='https://anku5x.github.io/drumset/'>Drum Set</a>
                <p>A responsive drum set app designed with JavaScript, jQuery and Bootstrap</p>
            </div>
            {
              //card 4: MoveIt
            }
                <div className='lg:w-40 lg:h-40 w-28 h-28'>
                  <img className='lg:w-48 lg:h-28 md:w-48 md:h-28 w-16 h-9' src={MoveIt} alt='move it'/>
                <a className='font-semibold hover:underline' href='https://anku5x.github.io/moveit/'>MoveIt</a>
                <p>A frontend only responsive website made with Bootstrap.</p>
            </div>
        </div>
      </div>
      </Layout>
   </>
  )
}
export default Projects
